@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Open Sans', 'Lato',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@-moz-document url-prefix() {
  .background {
    background: radial-gradient(farthest-corner at 40px 40px,
      #6076C4 0%, #C460A5 75%, #28235F 100% );
  }
}

.header {
    position: absolute;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    text-decoration: none;

    height: 75px;
    width: 100vw;
}

.header .left {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    
    margin-left: 50px;
}

.header .right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    margin-right: 50px;

}

.cursive {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    height: 50px;
    width: 150px;

    text-decoration: none;
    font-family: 'Cedarville Cursive', cursive;
    font-weight: bold;
    font-size: 24px;

    color: #FFFFFF;

    background: none;
    border-radius: 15px;

    margin-left: 25px;
}

.cursive:hover {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    font-family: 'Cedarville Cursive', cursive;
    font-weight: bold;
    text-decoration: none;

    height: 50px;
    width: 150px;

    color: #CCCCCC;

    border-radius: 15px;

    margin-left: 25px;
}

.button {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    height: 3.125em;
    width: 9.375em;

    font-family: "lato";
    font-style: normal;
    font-weight: bold;
    font-size: 0.875em;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;

    color: #000000;

    background: transparent;
    border-radius: 15px;

    margin-right: 25px;
}

.button:hover {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    font-family: "lato";
    font-style: normal;
    font-weight: bold;
    font-size: .875em;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;
    
    height: 3.125em;
    width: 9.375em;

    color: #000000;

    background: #ffffff;
    border-radius: 15px;

    margin-right: 25px;
}

.landing-button {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    height: 3.125em;
    width: 15.625em;

    text-decoration: none;
    font-family: 'lato';
    font-weight: bold;

    color: #FFFFFF;

    background: #2898FF;
    border-radius: 15px;

    margin-right: 25px;
    margin-bottom: 10px;
}

.landing-button:hover {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    height: 3.125em;
    width: 15.625em;

    text-decoration: none;
    font-family: 'lato';
    font-weight: bold;

    background: #0c5aa3;
    border-radius: 15px;

    margin-right: 25px;
    margin-bottom: 10px;
}

.landing-button .highlighted-text {
    font-family: Lato;
    font-style: normal;
    font-weight: bold;
    font-size: 0.875em;
    line-height: 100%;
    display: flex;
    align-items: center;
    text-align: center;
}
.landing {
    height: 100%;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;

    background-color: #f2f2f3;
    background-size: cover;
}

.landing .photo {
    position: absolute;
    margin-top: 7.5vh;
    margin-left: 50vh;
    width: 450px;
}

.landing .text-container table {
    width: 100%;
}   

.landing .text-container thead {
    align-items: center;
    text-align: center;
    height: 50px;
}  

.landing .text-container tbody {
    align-items: flex-start;
    text-align: flex-start;
    justify-content: center;
}  

.landing .text-container th {
    min-width: 50px;
}

.landing .text-container tr {
    height: 50px;
    min-height: 50px;
}

.landing .text-container tr:hover {
    background-color: mediumseagreen;
} 

.landing .text-container td {
    align-items: flex-start;
    text-align: flex-start;
}  

.landing .text-container {
    width: 85vw; 

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;
    margin-top: 100px;
    margin-left: 100px;
}

.landing .text-container p {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    line-height: 1.35em;

    margin-top: 10px;
    margin-bottom: 10px;

    color: #000000;
}

.landing .text-container ul {
    font-family: "Roboto";
    font-size: 1em;
    line-height: 1.5em;

    color: #000000;
}

.landing .text-container ol {
    font-family: "Roboto";
    font-size: 1em;
    line-height: 1.5em;

    color: #000000;
}


.landing .text-container h1 {
    font-family: "Roboto";
    font-size: 2em;

    line-height: 2em;
    margin-top: 0px;
    margin-bottom: 0px;

    color: #000000;
}

.landing .text-container h2 {
    font-family: "Roboto";
    font-size: 1.5em;

    line-height: 1.5em;
    margin-top: 0px;
    margin-bottom: 0px;

    color: #000000;
}

.landing .text-container h3 {
    font-family: "Roboto";
    font-size: 1em;

    line-height: 1.35em;
    margin-top: 0px;
    margin-bottom: 0px;
    
    color: #000000;
}


.landing .button-container {
    width: 30vw; 
    
    display: flex;

    align-items: flex-end;
    justify-content: flex-end;

    flex-direction: column;
}
.external-button {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    height: 50px;
    width: 200px;

    text-decoration: none;
    font-family: 'lato';
    font-weight: bold;

    color: black;

    background: mediumseagreen;
    border-radius: 15px;
}

.external-button:hover {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    font-family: 'lato';
    font-weight: bold;
    text-decoration: none;

    height: 50px;
    width: 200px;

    color: #000000;

    background: seagreen;
    border-radius: 15px;
}

.resources {
    min-height: 100vh;
    height: 100%;
    overflow: auto;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;

    background-color: #f2f2f3;
    background-size: cover;
}

.resources hr.solid {
    border-top: 100px solid black;
}

.resources .photo {
    position: absolute;
    margin-top: 7.5vh;
    margin-left: 50vh;
    width: 450px;
}

.resources .header-container {
    width: 80vw;
    display: flex;

    align-items: center;
    justify-content: center;

    flex-direction: column;

    margin-top: 15vh;
    margin-left: 10vw;
}

.resources .text-container {
    width: 80vw;
    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;

    margin-top: 25px;
    margin-left: 10vw;
}

.resources .header-container h1 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 2em;
    margin-top: 0px;
    margin-bottom: 10px;
}

.resources .header-container h2 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 1.6em;
    margin-top: 0px;
    margin-bottom: 45px;
}

.resources .text-container h3 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 1em;
    margin-bottom: 0px;
}

.resources .text-container p {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    line-height: 1.35em;
    margin-top: 0px;
    margin-bottom: 0px;
}

.resources .text-container li {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    margin-top: 0px;
    margin-bottom: 5px;
}

.resources .text-container .small-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 800;
    font-size: 0.875em;
    display: flex;

    color: #000000;
}

.resources .text-container .big-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 800;
    font-size: 2.25em;
    
    color: #000000;

    margin-top: 0.1em;
    margin-bottom: 1em;
}

.resources .button-container {
    width: 100vw; 
    
    display: flex;

    align-items: center;
    justify-content: center;

    flex-direction: column;

    margin-top: 25px;
}
